ul.uw-sidebar-menu,
ul.uw-mobile-menu {
	margin-left: 20px;
	margin-right: -20px;
	padding: 20px;
	margin-top: -124px;
	background-color:white;

	ul,
  li {
		display: block;
		list-style: none;
		padding: 0;
		margin: 0;
		-webkit-font-smoothing: auto;
	}

	li.pagenav,
	>div {
    a,
    span {
      // font-family: @font-family-headline;
      display: block;
      font-size: 16px;
      padding: 10px 10px 10px 20px;
      &.homelink {
        display: none;
        color: @purple;
        font-size: 25px;
      }
    }
    ul {
      line-height: 1.3;
      font-size: 19px;
      li.current_page_item,
      li.current_page_parent {
        background-color: @purple;
        & > a,
        & > span {
          font-weight: bold;
          padding-left: 19px;
          padding-top: 16px;
          padding-bottom: 16px;
        }
        a,
				span {
          color: #fff;
          border-bottom: none;
          border-left: 10px solid @lightgold;
        }
				&.current_page_ancestor {
          position: relative;
          & > a {
            padding-right: 60px;
            &:hover,
            &:focus {
              color: @lightgold;
            }
            &:after {
              content: "";
              background: url(assets/svg/uw-sprite.svg) no-repeat -191px -478px;
              display: inline-block;
              height: 52px;
              width: 44px;
              margin-left: 7px;
              margin-top: -26px;
              position: absolute;
              right: 0;
              top: 50%;
              background-color: @lightgold;
            }
          }
          a,
          span {
            border-left: none;
            position: relative;
          }
        }
        li.page_item_has_children ul {
          display: none;
        }
        li.page_item_has_children.current_page_item ul {
          display: block;
        }
      }
      li {
        a	{
          color: @purple;
          border-bottom: thin solid #eee;
        }
        &.current_page_item ul.children,
        &.current_page_ancestor ul.children {
          display: block;
          font-size: 17px;
          background-color: #fff;
          li {
            background-color: #eee;
            border-bottom: 1px solid #fff;
            a,
            span {
              color: @darkgray;
              padding: 10px 30px 10px 30px;
              border-left: none;
              &:hover,
              &:focus {
                background-color: #ddd;
              }
            }
          }
          li.current_page_item {
            // background-color:white;
            ul.children {
              li {
                border-bottom: 1px solid #e0e0e0;
              }
              li:last-child {
                border-bottom: none;
              }
              a {
                background-color: #fff;
                color: @darkgray;
                border-left: none;
                padding-left: 25px;
                &:before {
                  content: "- ";
                }
              }
            }
            span  {
              // background-color:#757575;
              border-left: 10px solid @lightgold;
              padding-left: 20px;
              background-color: #ddd;
              a {
                color: #fff;
                border-bottom: none;
              }
              // color:white;
              border-bottom: none;
            }
          }
        }
      }
      li:last-child {
        a {
          // color:@purple;
          border-bottom: none;
        }
      }
    }
  }

  ul.children {
    display: none;
  }
}

// Sidebar widget titles
.uw-sidebar {
	.widgettitle,
	h2.block-title {
    clear: left;
		font-weight: @ultra;
		font-size: 22px;
		text-transform: uppercase;
		position: relative;
		padding-bottom: 20px;
		&:after,
		&:before {
			position: absolute;
			left: 0;
			bottom: 5px;
			content: "";
			height: 4px;
		}
		&:before {
			width: 100px;
			background-color: @lightgold;
		}
		&:after {
			width: 40px;
			.skew(-25deg,0);
			left: 80px;
			background-color: #fff;
			bottom: 3px;
			height: 8px;
		}
	}
}

// Sidebar region
.uw-sidebar {
  .region:not(:last-child) {
    padding-bottom: 30px;
  }
}

@media only screen and (max-width: @screen-md-min) {
  .uw-sidebar {
    .region {
      padding-top: 30px;
    }
  }
}

body.home .uw-sidebar-menu {
	display: none;
}

/* Medium Devices, Desktops */
@media only screen and (max-width : @screen-md-min) {
	ul.uw-sidebar-menu {
		padding: 0;
		margin: 50px 0 0 0;
		clear: both;
	}
}

@media only screen and (min-width : @screen-sm-min) {
	.uw-sidebar {
		padding-top: 40px;
	}
}
