// Overlay
.uw-overlay {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	z-index: 999;
	cursor: -webkit-zoom-out;
	background-color: rgba(255,255,255,.95);

	div.wrapper {
    position:absolute;
    top: 50%;
    left : 50%;
		p {
			font-size: 15px;
			line-height: 22px;
			margin-top: 15px;
		}
		img {
			border: 1px solid #ececec;
		}
	}
}

@media only screen and (max-width : 1024px) {
	.uw-overlay {
		cursor: pointer;
	}
}

@media only screen and (max-width : @screen-xs-max) {
	.uw-overlay {
		cursor: pointer;
	}
	.alignright,
	.alignleft,
	.alignnone {
		float: none;
		margin: 20px 20px 20px 0px;
		width: 100% !important;
		height: auto;
	}
}
