// Menu using icons

.icon-menu {
    text-align: center;
    margin: 40px 0;
    border-top: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
    ul {
      padding-left: 0;
    }
  li {
    list-style: none;
    text-align: center;
    display: inline-block;
    padding: 30px;
    position: relative;
//    &:after {
//        content: "";
//        display: block;
//        width: 1px;
//        height: 100px;
//        background-color: #ddd;
//        position: absolute;
//        right: -10px;
//        top: 50%;
//        margin-top: -50px;
//        .skew(-15deg, 0);
//    }
  }
  a {
    line-height: @headings-line-height;
    font-family: @font-family-headline;
    font-size: 16px;
    font-weight: 900;
    color: @purple;
      display: inline-block;
      padding-top: 100px;
      margin-top: -85px;
    .transition(all .5s cubic-bezier(0.165, 0.84, 0.44, 1));
    &:hover,
    &:focus {
      .translate(0, -10px);
    }
  }
}


